@use "./vars" as *;
@use "./mixins" as *;

.header_cont {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 25px 0px 25px;
  position: relative;
  @include breack-pint-up(small) {
    padding: 55px 170px 0px 170px;
  }
}
.hamburger {
  display: flex;
  flex-direction: column;
  :not(:last-child) {
    margin-bottom: 6px;
  }
  cursor: pointer;
}
.hamburger_line {
  width: 24px;
  height: 3px;
  background-color: $Gray;
}
.header_left {
  display: flex;
  :first-child {
    margin-right: 40px;
  }
  :not(:first-child) {
    margin-left: 30px;
  }
  a {
    text-decoration: none;
    color: $Gray;
  }
}
.header_btns {
  display: flex;
  align-items: center;
}
.login {
  color: $Gray;
  border: none;
  background: none;
  margin-right: 40px;
  font-size: 13px;
}
.sign {
  background-color: $Cyan;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 18px;
}
///ham_cont
.ham_cont {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 75px);
  width: 85vw;
  height: 385px;
  background-color: $DarkViolet;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  z-index: 2;
}
.ham_content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
  a {
    text-decoration: none;
    color: white;
    font-size: 16px;
  }
}
.line {
  height: 1px;
  width: 70vw;
  background-color: $Gray;
}
.ham_login {
  border: none;
  background: none;
  color: white;
  font-size: 18px;
}
.ham_sign {
  border: none;
  background-color: $Cyan;
  color: white;
  font-size: 18px;
  padding: 17px 0;
  border-radius: 30px;
}
