$breack_points_up: (
  "small": "400px",
  "medium": "1200px",
);
$breack_points_down: (
  "medium": "399px",
  "large": "1199px",
);

@mixin breack-pint-up($size) {
  @media (min-width: map-get($breack_points_up,$size)) {
    @content;
  }
}
@mixin breack-pint-down($size) {
  @media (max-width: map-get($breack_points_down,$size)) {
    @content;
  }
}
