@use "./vars" as *;
@use "./mixins" as *;
.footer_first {
  background-image: url(../images/bg-boost-mobile.svg);
  background-size: cover;
  background-position: center;
  background-color: $DarkViolet;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  padding: 105px 40px;
  text-align: center;
  align-items: center;
  h1 {
    color: white;
    font-size: 24px;
  }
  button {
    background-color: $Cyan;
    border: none;
    padding: 20px 40px;
    color: white;
    border-radius: 25px;
    width: fit-content;
    margin-top: 30px;
    cursor: pointer;
  }
}
.footer_socond {
  background-color: $VeryDarkViolet;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 55px 0;
  @include breack-pint-up(small) {
    flex-direction: row;
  }
}
.footer_logo {
  margin-bottom: 60px;
}
.footer_links_cont {
  @include breack-pint-up(small) {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    width: 100%;
  }
}
.footer_links {
  display: flex;
  flex-direction: column;
  margin-bottom: 45px;
  gap: 15px;
  @include breack-pint-up(small) {
    margin-top: 0;
  }
  a {
    color: $GrayishViolet;
    text-decoration: none;
  }
  h2 {
    color: white;
    margin-bottom: 15px;
  }
}
.footer_meida {
  :not(:last-child) {
    margin-right: 24px;
  }
}
