@use "./vars" as *;
@use "./mixins" as *;

.baner_cont {
  margin-top: 25px;
  overflow: hidden;
  @include breack-pint-up(small) {
    display: flex;
    flex-direction: row-reverse;
    margin-top: 75px;
  }
  img {
    max-width: 120vw;
    transform: translateX(50px);
    @include breack-pint-up(small) {
      transform: translateX(150px);
    }
  }
}
.baner_text {
  margin-top: 45px;
  text-align: center;
  padding: 0 30px;
  @include breack-pint-up(small) {
    margin-top: 75px;
    text-align: left;
  }
  h1 {
    font-size: 30px;
    color: $VeryDarkViolet;
    @include breack-pint-up(small) {
      font-size: 75px;
    }
  }
  p {
    margin-top: 25px;
    color: $GrayishViolet;
    @include breack-pint-up(small) {
      font-size: 18px;
    }
  }
  button {
    margin-top: 35px;
    font-size: 17px;
    background-color: $Cyan;
    color: white;
    border: none;
    padding: 12px 24px;
    border-radius: 18px;
  }
}
