@use "./vars" as *;
@use "./mixins" as *;

.contet_title {
  @include breack-pint-up(small) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.content_cont {
  position: relative;
  padding: 170px 40px 80px 40px;
  margin-top: 170px;
  background-color: $GrayBack;
  text-align: center;
  @include breack-pint-up(small) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  h1 {
    color: $VeryDarkViolet;
    @include breack-pint-up(small) {
      font-size: 24px;
    }
  }
  p {
    color: $GrayishViolet;
    margin-top: 33px;
    @include breack-pint-up(small) {
      font-size: 14px;
      max-width: 80vh;
    }
  }
}
.tab_cont {
  background-image: url("../images/bg-shorten-mobile.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: $DarkViolet;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 24px;
  width: 85%;
  border-radius: 10px;
  @include breack-pint-up(small) {
    display: flex;
    align-items: center;
    padding: 52px 64px;
  }
  input {
    width: 100%;
    padding: 16px 16px;
    margin-bottom: 5px;
    border-radius: 5px;
    border: none;
    @include breack-pint-up(small) {
      margin: 0 24px 0 0;
      width: 100%;
      padding: 24px 42px;
      font-size: 17px;
    }
  }
  button {
    border-radius: 5px;
    width: 100%;
    padding: 16px 16px;
    background-color: $Cyan;
    border: none;
    color: white;
    cursor: pointer;
    margin-top: 5px;
    @include breack-pint-up(small) {
      max-width: fit-content;
      margin: 0;
      padding: 24px 42px;
      font-size: 17px;
    }
  }
}

.components_cont {
  margin-top: 100px;
  @include breack-pint-up(small) {
    display: flex;
  }
}
.component {
  position: relative;
  background-color: white;
  padding: 85px 35px 45px 35px;
  text-align: center;
  border-radius: 5px;
  margin-bottom: 90px;

  @include breack-pint-up(small) {
    text-align: left;
  }
  img {
    position: absolute;
    top: 0;
    transform: translate(-50%, -50%);
    background-color: $DarkViolet;
    padding: 24px;
    border-radius: 50%;
    @include breack-pint-up(small) {
      transform: translate(0%, -50%);
    }
  }
  &::after {
    content: "";
    position: absolute;
    height: 90px;
    width: 8px;
    background-color: $Cyan;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 100%);
    @include breack-pint-up(small) {
      height: 0;
    }
  }
  h1 {
    color: $DarkViolet;
    margin-bottom: 25px;
  }
  p {
    color: $Gray;
    @include breack-pint-up(small) {
      font-size: 12px;
    }
  }
  @include breack-pint-up(small) {
    margin-right: 30px;
  }
}
.component_second {
  position: relative;
  background-color: white;
  padding: 85px 35px 45px 35px;
  text-align: center;
  border-radius: 5px;
  margin-bottom: 90px;
  @include breack-pint-up(small) {
    text-align: left;
    margin-top: 50px;
    margin-bottom: auto;
  }
  img {
    position: absolute;
    top: 0;
    transform: translate(-50%, -50%);
    background-color: $DarkViolet;
    padding: 24px;
    border-radius: 50%;
    @include breack-pint-up(small) {
      transform: translate(0%, -50%);
    }
  }
  &::after {
    content: "";
    position: absolute;
    height: 90px;
    width: 8px;
    background-color: $Cyan;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 100%);
    @include breack-pint-up(small) {
      height: 8px;
      width: 30px;
      left: 0;
      bottom: 50%;
      transform: translateX(-100%);
    }
  }

  h1 {
    color: $DarkViolet;
    margin-bottom: 25px;
  }
  p {
    color: $Gray;
    @include breack-pint-up(small) {
      font-size: 12px;
    }
  }
  @include breack-pint-up(small) {
    margin-right: 30px;
  }
}
.component_last {
  position: relative;
  background-color: white;
  padding: 85px 35px 45px 35px;
  text-align: center;
  border-radius: 5px;

  @include breack-pint-up(small) {
    text-align: left;
    margin-top: 100px;
  }
  h1 {
    color: $DarkViolet;
    margin-bottom: 25px;
    @include breack-pint-up(small) {
      font-size: 18px;
    }
  }
  p {
    color: $Gray;
    @include breack-pint-up(small) {
      margin-right: 30px;
    }
  }
  img {
    position: absolute;
    top: 0;
    transform: translate(-50%, -50%);
    background-color: $DarkViolet;
    padding: 24px;
    border-radius: 50%;
    @include breack-pint-up(small) {
      transform: translate(0%, -50%);
    }
  }
  &::after {
    @include breack-pint-up(small) {
      content: "";
      position: absolute;
      background-color: $Cyan;
      height: 8px;
      width: 30px;
      left: 0;
      bottom: 50%;
      transform: translateX(-100%);
    }
  }
}
