@use "./scss/vars" as *;
@use "./scss/mixins" as *;
@use "./scss/header" as *;
@use "./scss/baner" as *;
@use "./scss/content" as *;
@use "./scss/footer" as *;
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap");

* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.DN_FOR_SMALL {
  @include breack-pint-down(medium) {
    display: none;
  }
}
.DN_FOR_MEDIUM {
  @include breack-pint-up(small) {
    display: none;
  }
}
